/*Contenedores*/

.foot {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3.5rem;
  background-color: #fe77ff;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  
}
.redes, .tecnologias, .portfolio {
  padding: 0.1em;
} 

/*Texto*/
.redes, .tecnologias{
  font-size: 2em;
  margin-top: 0.2em;
}
.redes {
  grid-column: 1/2;
  text-align: left;
}
.portfolio {
  grid-column: 2/3;
  text-align: center;
  font-size: 15px;
  margin-top: 1.3em;
}
.portfolio a:hover {
  color: #818581;
 
}
.tecnologias {
  grid-column: 3/4;
  text-align: right;
}
a{
  text-decoration: none;
  color: black;
}
a:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.in:hover {
  color: rgb(4, 4, 207);

}
.wpp:hover {
  color: rgb(5, 161, 5);
}
.gh:hover{
  color: purple;
}

.header-container {
  position: fixed;
  top:0;
  width: 100%;
  left: 0;
}
.nya {
  width: 2.5em;
  padding-top: 10px;
}

.down {
  height: 70px;
  text-align: center;
  vertical-align: center;
  display: inline-block;
  font-size: 20px;
  font-family: 'Mountains of Christmas', serif;
  color: rgb(253, 253, 168);
  position: relative;
  transition: 0.4s;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9b4;
  max-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  transition: 0.4s;
  margin-top: 10px;
}
.down:hover .dropdown-content {
  display: block;
  transition: 0.4s;
}
ul {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  width: 100%;
  padding: 0;
  margin: 0;
}
li {
  text-align: left;
  line-height: 25px;
  font-size: 12px;
  padding: 3px;
  color: black;
}
.change {
  border-bottom: 2px solid black;
}
li:hover {
  background-color: teal;
}
.change:hover {
  background-color: unset;
}

button {
  background-color: transparent;
  padding: 5px;
  margin: 5px;
}
* {
  margin:0;
  padding:0;
  box-sizing: border-box;
  transition: 0.2s;
  
  font-family: 'Press Start 2P', serif;
}

body {
  background-image: url("data:image/png;base64,");
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
  transition: 0.2s;
}
